import React, { Component } from 'react';
import {BrowserRouter as Router,Link,Route} from 'react-router-dom';
import '../Component/Home.css';
import All from './Home/All';
import Jinghua from './Home/Jinghua';
import Share from './Home/Share';
import Ask from './Home/Ask';
import Job from './Home/Job';
import Dev from './Home/Dev';
import Content3 from './Content3';



export default class Home extends Component {
    render() {
        let {url}=this.props.match;//this.props.match.url='/home';
        console.log(this.props);
        return (
            <div className="home">
                <div className="home1">
                    <Link to={`${url}/all`}>全部</Link>
                    <Link to={`${url}/jinghua`}>精华</Link>
                    <Link to={`${url}/share`}>分享</Link>
                    <Link to={`${url}/ask`}>问答</Link>
                    <Link to={`${url}/job`}>招聘</Link>
                    <Link to={`${url}/dev`}>客户端测试</Link>
                </div>
               
                <div>
                    
                    <Route exact path={`${url}/all`} component={All}/>
                    <Route exact path={`${url}/jinghua`} component={Jinghua}/>
                    <Route exact path={`${url}/share`} component={Share}/>
                    <Route exact path={`${url}/ask`} component={Ask}/>
                    <Route exact path={`${url}/job`} component={Job}/>
                    <Route exact path={`${url}/dev`} component={Dev}/> 

                    <Route exact path={`${url}/all/content/:id`} component={Content3}/>
                    <Route exact path={`${url}/good/content/:id`} component={Content3}/>
                    <Route exact path={`${url}/share/content/:id`} component={Content3}/>
                    <Route exact path={`${url}/ask/content/:id`} component={Content3}/>
                    <Route exact path={`${url}/job/content/:id`} component={Content3}/>
                    <Route exact path={`${url}/dev/content/:id`} component={Content3}/>



                    <Route exact path={`${url}/all/:page`} component={All}/>
                    <Route exact path={`${url}/good/:page`} component={Jinghua}/>
                    <Route exact path={`${url}/share/:page`} component={Share}/>
                    <Route exact path={`${url}/ask/:page`} component={Ask}/>
                    <Route exact path={`${url}/job/:page`} component={Job}/>
                    <Route exact path={`${url}/dev/:page`} component={Dev}/>



                </div>
                
            </div>
        )
    }
}
