import React, { Component } from 'react';
import '../Component/Content3.css';

export default class Content3 extends Component {
    constructor(){
        super();
        this.state={
            content:[]
        }
    }
    componentDidMount(){//组件调完后发强求
        console.log(this.props);
            this.setState({
                content:this.props.location.state.content
            })
    }
    
    render(){
        return (
            <div className="back">
                <div className="back1">
                    <span>分享</span>
                    <h2>{this.props.location.state.title}</h2>
                    <p>.作者 {this.props.location.state.author}.{this.props.location.state.views}次浏览.来自 分享</p>
                </div>
                <div className="back2" dangerouslySetInnerHTML={{__html:this.state.content}}></div> 
            </div>
        )
    }
}
